import { LoadViewMessageAction } from './../message-action';
import { MessageService } from './../../services/message.service';
import { AppState } from './../message-state';
import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Rx';
import { ActivatedRoute } from '@angular/router';
import { IMail } from '../../model/IMessage';
import * as MD5 from './../../md5';

@Component({
  selector: 'sp-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.css']
})
export class ViewMessageComponent implements OnInit {

  email = 'default@schoolpop.ng';
  mail$: Observable<IMail>;
  subject$: Observable<string>;
  email$: Observable<string> = Observable.of(this.email);
  fullname$: Observable<string>;
  message$: Observable<string>;
  hash$: Observable<string>;


  constructor(private store: Store<AppState>, private ms: MessageService, private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.mail$ = this.route.params.map(params => params['id']).flatMap(id => {
      const objectId = id;
      return this.store.skip(1).flatMap(state => {
        return this.getMail(state, id);
      });
    });
    this.subject$ = this.mail$.map(m => m.subject);
    this.fullname$ = this.mail$.map(m => m.from.firstName + ' ' + m.from.lastName);
    this.email$ = this.mail$.map(m => m.from.email);
    this.hash$ = this.email$.map(e => this.getSrc(e));
    this.message$ = this.mail$.map(m => m.message.text);

    // this.route.params.map(params => params['id']).subscribe(
    //   (id:string) => this.store.dispatch(new LoadViewMessageAction(id))
    // )

  }

  getMailFromInbox(state: AppState, id: string): Observable<IMail> {
    return Observable.of(state.dataState.inbox[id]);
  }

  getMailFromOutbox(state: AppState, id: string): Observable<IMail> {
    return Observable.of(state.dataState.outbox[id]);
  }

  getMail(state: AppState, id: string): Observable<IMail> {
    return Observable.concat(this.getMailFromInbox(state, id), this.getMailFromOutbox(state, id)).take(1);
  }

  getSrc(email): string {
    const baseUrl = 'https://www.gravatar.com/avatar/';
    const hash = MD5.encrypt(email);
    return baseUrl + hash;
  }

}



