import { MessageComponent } from './message.component';
import { ViewMessageComponent } from './view/view.component';
import { Routes } from '@angular/router';
import { BasicComponent } from '../commons/layout/basic/basic.component';
import { ComposeComponent } from './compose/compose.component';
import { ComposePopUpComponent } from './compose-pop-up/compose-pop-up.component';
import { InboxComponent } from './inbox/inbox.component';
import { OutboxComponent } from './outbox/outbox.component';
export const routes: Routes = [
  {
    path: '',
    component: BasicComponent,
    children: [
      {
        path: '',
        component: MessageComponent
      },
      {
        path: 'compose',
        component: ComposeComponent,
        children: [
          {
            path: ':id',
            component: ComposePopUpComponent
          }

        ]
      },
      {
        path: 'inbox',
        component: InboxComponent
      },
      {
        path: 'outbox',
        component: OutboxComponent
      },
      {
        path: ':id',
        component: ViewMessageComponent
      }
    ]
  }
];



