import { AppState} from './../message-state';
import { Store } from '@ngrx/store';
import { IMail } from './../../model/IMessage';
import { Component, OnInit } from '@angular/core';
import { MomentUtil } from '../../moment-util';
import * as _ from 'lodash';

@Component({
  selector: 'sp-outbox',
  templateUrl: './outbox.component.html',
  styleUrls: ['./outbox.component.css']
})
export class OutboxComponent implements OnInit {

  outbox: IMail[] = [];

  moment = new MomentUtil();

  constructor(private store: Store<AppState>) {
    store.subscribe(
      state => {
        this.outbox = _.values(state.dataState.outbox)
      }
    );
  }

  ngOnInit() {
  }

  formatTime(date) {
    return this.moment.getMoment()(date).fromNow();
  }

}



