import { FileUploadModel } from './../../commons/widget/file-uploader/file-uploader.component';
import { AsyncValidator } from './../../commons/directives/async-validator.directive';
import { Router } from '@angular/router';
import { LoadingService } from './../../services/loading.service';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { MessageService } from '../../services/message.service';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { initCompose } from '../../app.js.helpers';
import { DomSanitizer } from '@angular/platform-browser';
import { MatIconRegistry, MatSnackBar } from '@angular/material';
import { validateEmail } from '../../commons/validators/validators';
import { UserService } from '../../services/user.service';
import * as _ from 'lodash';

@Component({
  selector: 'sp-compose',
  templateUrl: './compose.component.html',
  styleUrls: ['./compose.component.css']
})
export class ComposeComponent implements OnInit {

  @Output("onMessage")
  messageEmitter: EventEmitter<any> = new EventEmitter<any>();

  composeForm:FormGroup;
  to: FormControl = new FormControl(['Enter Username or Email']);
  fileList:FileUploadModel[] = [];
  fileListClone:FileUploadModel[] = [];
  attachment:any[] = [];

  constructor(private ls:LoadingService,
      private ms: MessageService,
      private us:UserService,
      iconRegistry: MatIconRegistry,
      public snackBar: MatSnackBar,
      sanitizer: DomSanitizer,
      private router:Router, private fb:FormBuilder) {

    iconRegistry.addSvgIcon(
      'png',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/png.svg'));

    iconRegistry.addSvgIcon(
      'file',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/file.svg'));

    iconRegistry.addSvgIcon(
      'svg',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/svg.svg'));

    iconRegistry.addSvgIcon(
      'pdf',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/pdf.svg'));


        this.composeForm = fb.group({
          to: ['', Validators.required],
          subject: ['', Validators.required],
          text: ['', Validators.required]
        })
  }

  ngOnInit() {
    this.composeForm.controls['to'].valueChanges
      .filter(val => val.length >= 2)
      .debounceTime(500)
      .distinctUntilChanged()
      .switchMap((val:string) => this.us.userExists(val))
      .subscribe(
        u => {
          if (u != null) {
            this.composeForm.controls['to'].setErrors(u);
          }
        },
        e => console.log(e)
      );
  }

  onSubmit() {
    let message: any = this.composeForm.value;
    message.attachments = this.attachment;
    this.messageEmitter.emit(message);
  }

  onDrop(e:File) {
    if (this.fileList.length == 3) {
      this.showSnack("Maximum number of files attached", 2000);
    } else {
      if (e.type.match('image/*') || e.type === 'application/pdf') {
        this.fileList.unshift({
          file: e,
          isDirty: true
        });
      } else {
        this.showSnack("This file format is not supported", 2000);
      }
    }
  }

  addToAttachments(attachment) {
    this.attachment.push(attachment.pf);
    this.fileListClone.push(attachment.clone);
  }

  showSnack(message: string, duration: number, action?:any) {
    this.snackBar.open(message, action, {
      duration: duration
    })
  }

  canSend():boolean {
    _.each(this.fileListClone, file => {
      if (file.isDirty) {
        return false;
      }
    })
    return true;
  }

  getIcon(file:File):string {
    if (file.type.match('image/*')) {
      return 'png';
    } else if (file.type === 'application/pdf') {
      return 'pdf';
    }
    switch (file.type) {
      case 'image/svg+xml':
        return 'svg';
      default:
        return 'file';
    }
  }

}



