import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { MessageVM } from './message-vm';
import { RouteService } from './../services/route.service';
import { MessageService } from './../services/message.service';
import { Store } from '@ngrx/store';
import { AppState } from './message-state';
import { LoadDashBoardAction, ChangeCurrentMessageSection, SendMessageAction } from './message-action';
import { IMail } from '../model/IMessage';
import * as _ from 'lodash';
import { Observable } from 'rxjs/Rx';
import { WindowRefService } from '../services/window-ref.service';

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MessageComponent implements OnInit {

  inbox: IMail[] = [];
  outbox: IMail[] = [];
  mail: IMail;
  currentSection = 0;
  inboxUnreadCount: number;
  private _window: Window;

  constructor(private store: Store<AppState>,
    private ms: MessageService,
    private rs: RouteService,
    private ref: ChangeDetectorRef,
    windowRef: WindowRefService) {
    this._window = windowRef.nativeWindow;
  }

  ngOnInit() {
    this.rs.setBc('sticky-header');
    this._window.scroll(0, 0);
    this.store.subscribe(
      state => {
        this.inbox = _.values(state.dataState.inbox);
        this.outbox = _.values(state.dataState.outbox);
        this.currentSection = state.uiState.currentMailSection;
        this.mail = state.viewMessageState.message;
        this.inboxUnreadCount = state.uiState.currentUnread;
        this.ref.markForCheck();
      }
    );
  }

  changeCurrentMessageSection(section: number) {
    this.store.dispatch(new ChangeCurrentMessageSection(section));
  }

  getMessages(): MessageVM[] {
    if (this.currentSection === 0) {
      return this.getMessageVMFrom(this.inbox);
    } else if (this.currentSection === 1) {
      return this.getMessageVMTo(this.outbox);
    }
  }

  getMessageVMFrom(m: IMail[]): MessageVM[] {
    const dataset: MessageVM[] = [];
    _.each(m, (message) => {
      const mail: MessageVM = <MessageVM>{
        objectId: message.objectId,
        fullName: message.from.firstName + ' ' + message.from.lastName,
        subject: message.subject,
        createdAt: message.createdAt,
        isRead: message.isRead,
      };
      dataset.push(mail);
    });
    return dataset;
  }

  getMessageVMTo(m: IMail[]): MessageVM[] {
    const dataset: MessageVM[] = [];
    _.each(m, (message) => {
      const mail: MessageVM = <MessageVM>{
        objectId: message.objectId,
        fullName: message.to.firstName + ' ' + message.to.lastName,
        subject: message.subject,
        isRead: true,
        createdAt: message.createdAt
      };
      dataset.push(mail);
    });
    return dataset;
  }

  getLabel() {
    switch (this.currentSection) {
      case 0:
        return 'Inbox';
      case 1:
        return 'Sent';
      case 3:
        return 'Compose';
      default:
        return 'Inbox';
    }
  }

  dispatchMessage(message: any) {
    this.store.dispatch(new SendMessageAction(message));
  }

}



