import { AppState } from './../message-state';
import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { IMail } from '../../model/IMessage';
import { MomentUtil } from '../../moment-util';
import { Router } from '@angular/router';
import * as _ from 'lodash';

@Component({
  selector: 'sp-inbox',
  templateUrl: './inbox.component.html',
  styleUrls: ['./inbox.component.css']
})
export class InboxComponent implements OnInit {

  inbox: IMail[] = [];

  moment = new MomentUtil().getMoment();

  constructor(private store:Store<AppState>, private router:Router) { 
    store.subscribe(
      state => {
        this.inbox = _.values(state.dataState.inbox)
      }
    );
  }

  ngOnInit() {
  }

  formatTime(date) {
    return this.moment(date).fromNow();
  }

  viewMessage(mail:IMail) {
    this.router.navigate(['/message', mail.objectId]);
  }

}



