import { LayoutModule } from '../commons/layout/layout.module';
import { routes } from './message.route.module';
import { LoadingService } from '../services/loading.service';
import { RouteService } from '../services/route.service';
import { ParseService } from '../services/parse.service';
import { UserService } from '../services/user.service';
import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MessageComponent } from './message.component';
import { ComposeComponent } from './compose/compose.component';
import { ComposePopUpComponent } from './compose-pop-up/compose-pop-up.component';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { WidgetModule } from '../commons/widget/widget.module';
import { MessageService } from '../services/message.service';
import { messageStoreReducer } from './reducers/inbox-reducer';
import { InboxComponent } from './inbox/inbox.component';
import { OutboxComponent } from './outbox/outbox.component';
import { ReactiveFormsModule } from '@angular/forms';
import { FroalaEditorModule, FroalaViewModule } from 'angular-froala-wysiwyg';
import { DirectivesModule } from '../commons/directives/directives.module';
import { MatIconModule, MatIconRegistry, MatSnackBarModule } from '@angular/material';
import { ViewMessageComponent } from './view/view.component';

@NgModule({
  imports: [
    CommonModule,
    LayoutModule,
    ReactiveFormsModule,
    MatIconModule,
    MatSnackBarModule,
    WidgetModule,
    DirectivesModule,
    FroalaEditorModule.forRoot(), FroalaViewModule.forRoot(),
    RouterModule.forChild(routes)
  ],
  declarations: [MessageComponent, ComposeComponent, ComposePopUpComponent, InboxComponent, OutboxComponent, ViewMessageComponent],
  providers: [
  ]
})
export class MessageModule { }



